@import url('https://fonts.googleapis.com/css2?family=Barlow+Condensed:ital,wght@1,200&family=Lato:wght@300&display=swap');

.calculator {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 400px;
    height: 300px;
    padding: 20px;
    margin-top: 4rem;
    background: radial-gradient(circle, rgba(238,174,202,1) 0%, rgba(148,187,233,1) 100%); /*#f0cdcd;*/
    color: #0e1f36;
    border: 3px solid #ac0606;
    border-radius: 5px;
}

.measures input{
    width: 100%;
    height: 35px;
    outline: none;
    border: none;
    text-align: center;
    font-size: 18px;
    font-family: 'Barlow Condensed', sans-serif;
    background-color: rgba(0, 0, 0, 0.6);
    color: #fff;
    font-weight: bolder;
    border-radius: 5px;
}

.btnCalc {
    width: 80px;
    padding: 5px;
    margin-top: 15px;
    font-size: 15px;
    background-color: #ac0606;
    color: #fff;
    border-radius: 5px;
    cursor: pointer;
}

.btnCalc:hover {
    background-color: #611010;
}

@media (max-width: 480px) {
    html {
        width: 100%;
        height: 100%;
        overflow-x: hidden;
    }
    .calculator {
        width: 300px;
        font-size: 15px;
    }
    .measures input{
        height: 15px;
        outline: none;
    }
}